import { Container, Row, Col } from 'react-bootstrap';
import LiComp from './LiComp';

function UlComp() {
    return(
        <div style={{backgroundColor: '#cdcaca', paddingTop: '5rem', paddingBottom: '5rem'}} >
            <Container>
                <h4 style={{fontWeight: '400', textAlign: 'center'}} >        :</h4>
                <Row style={{marginTop: '2rem'}} >
                    <Col md={6} >
                        <LiComp text=' ( -)' />
                        <LiComp text=' ' />
                        <LiComp text='    (  )' />
                        <LiComp text=',    ' />
                        <LiComp text=' ()  3x ' />
                        <LiComp text=' (   . 31, 33)' />
                    </Col>
                    <Col md={6} >
                        
                        <LiComp text=' ,   , ' />
                        <LiComp text='          
                                    (   . 15, 17),  ' />
                        <LiComp text='  ,   
                                    ,
                                   ( )' />
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default UlComp;