import { Container, Row } from "react-bootstrap";
import ContractCard from "./ContractCard";

function Contract({setModal}) {
    return(
        <div style={{backgroundColor: '#cdcaca'}} >
            <Container style={{paddingTop: '3.5rem', paddingBottom: '5rem'}} >
            <Row>
                <ContractCard setModal={setModal[0]} buttonStyle={{marginTop: '1rem'}} title="      ()     () " />
                <ContractCard setModal={setModal[1]} buttonStyle={{marginTop: '3.5rem'}} title="      ()   ( .)" />
                <ContractCard setModal={setModal[2]} buttonStyle={{marginTop: '2rem'}} title="      ()    ( )" />
            </Row>
           </Container>
        </div>
    );
}

export default Contract;